import React from 'react';
import Moment from 'moment';

const Message = ({ message: { name, date, message }, my_name }) => {
var datess = new Date(parseInt(date));
  return (
    <div>
      <h4>{name}:</h4>
        <p>{message}</p>
        <h5>{Moment(datess).format('DD-MM-YYYY HH:mm:ss')}</h5>
        
      </div>
  );
}

export default Message;
