import React, { useState, useEffect } from "react";
import io from "socket.io-client";
import "../my_styles.css"

import Input from '../Input/Input'
import Messages from '../Messages/Messages';
import Vuelos from '../Vuelos/Vuelos';
import ObtenerVuelos from '../ObtenerVuelos/ObtenerVuelos';
import MapComponent from '../MapComponent/MapComponent';
import {Container, Col, Row} from 'react-bootstrap';


const ENDPOINT = 'wss://tarea-3-websocket.2021-1.tallerdeintegracion.cl/';

let socket;

const Flights = () => {
  const [my_name, setName] = useState('');
  const [messages, setMessages] = useState([]);
  const [message, setMessage] = useState('');
  const [flights, setFlights] = useState([]);
  const [positions, setPositions] = useState([]);



  useEffect(() => {

    socket = io(ENDPOINT, {path: '/flights'});
    socket.emit('FLIGHTS');

  },[]);



  useEffect(() => {

    socket.on('FLIGHTS', flights_data => {
      // console.log(flights_data);
      setFlights(flights_data);
    });
  
  },[]);

  useEffect(() => {

    socket.on('CHAT', message => {
      setMessages(messages => [...messages, message]);
    });
  
  }, []);


  
  useEffect(() => {

    socket.on('POSITION', new_position => {
      setPositions(positions => [...positions, new_position]);
    })
  
  }, []);

  const sendMessage = (event) => {
    event.preventDefault();
    if(message) {
      socket.emit('CHAT', {message: message, name: my_name});
      setMessage('');
    }
  }

  const getFlights = () => {
    socket.emit('FLIGHTS');
  }

  function filterItems(code) {
    return positions.filter(function(el) {
        return el.code === code;
    })
  }

  function  lastItem(code) {
    const items = filterItems(code);
    if(items.length !== 0) {
      return items[items.length -1].position;
    }
    else {
      return [0,0];
    }
    }

  return (
  <div>
    <Container>
      <Row>
        <Col xs = {8}>
          <h1>Live Flights</h1>
          <MapComponent flights = {flights} positions = {positions} filterItems = {filterItems} lastItem = {lastItem} />
          <ObtenerVuelos getflights = {getFlights} />
        </Col>
        <Col xs = {4}>
          <h1>Live Chat</h1>
          <Messages messages={messages} name={my_name} />
          <input placeholder="Name" type="text" onChange={(event) => setName(event.target.value)} />
          <Input message={message} setMessage={setMessage} sendMessage={sendMessage} />
        </Col>
    </Row>
    </Container>
    <Vuelos flights = {flights} />
  </div>
  );
}

export default Flights;

