import React from 'react';

import Vuelo from '../Vuelo/Vuelo';

import {Container, Col, Row} from 'react-bootstrap';


const Vuelos = ({flights}) => {
    return (
      <Container>
        <Row>
          {flights.map((flight, i) => <Col xs = {3} key={i}><Vuelo flight={flight}/></Col>)}
        </Row>
      </Container>
    );
  }
  
  export default Vuelos;
