import axios from 'axios';
import React, {useState, useEffect } from 'react';
import {Container, Col, Row} from 'react-bootstrap';


const Vuelo = ({ flight:{code, airline, origin, destination, plane, seats, passengers}}) => {
  const [origin_adress, setOriginAdress] = useState("");
  const [destination_adress, setDestinationAdress] = useState("");

  useEffect(()=> {
    axios.get("https://api.mapbox.com/geocoding/v5/mapbox.places/"+String(origin[1])+","+String(origin[0])+".json?&access_token=pk.eyJ1IjoicmFpbW9uZGQwMjAzIiwiYSI6ImNraHowZnZodjBmOGwycWs2N3d5aWY4YW8ifQ.zbhiwYQxUa16IfTrNNVsEQ")
    .then(res => {setOriginAdress(res.data.features[0].place_name);}).catch(err => 
    {setOriginAdress("No Adress");
    console.log(err);});
})

  useEffect(()=> {
    axios.get("https://api.mapbox.com/geocoding/v5/mapbox.places/"+String(destination[1])+","+String(destination[0])+".json?&access_token=pk.eyJ1IjoicmFpbW9uZGQwMjAzIiwiYSI6ImNraHowZnZodjBmOGwycWs2N3d5aWY4YW8ifQ.zbhiwYQxUa16IfTrNNVsEQ")
    .then(res => {setDestinationAdress(res.data.features[0].place_name);
  }).catch(err => {setDestinationAdress("No Adress");
      console.log(err);});
  })

  
  return (
    <div>
      <h3>Flight {code}</h3>
      <p>Airline {airline}</p>
      <p>Plane {plane}</p>
      <p>Origin: {origin_adress}</p>
      <p>Destination: {destination_adress}</p>
      <p>Seats: {seats}</p>
      <p>Passengers:</p>
      <hr/>
      {passengers.map((passenger, i) => <div key={i}>name: {passenger.name} age: {passenger.age}</div>)}
    </div>
  );
}

export default Vuelo;
