import React from 'react';

import { Map, TileLayer, Marker, Popup, Polyline } from 'react-leaflet'
import L from 'leaflet'

import airport from "../../icons/airport.png"
import avion from "../../icons/avion.png"


const MapComponent = ({flights, positions, filterItems, lastItem}) => {
  var greenIcon = new L.icon({
    iconUrl: airport,

    iconSize:     [20, 20], // size of the icon
});

var avionIcon = new L.icon({
  iconUrl: avion,
  iconSize: [40, 30], // size of the icon
});

    return (
      <div>
        <Map
      className="map"
      center={[-30,-60]}
      zoom={4}
      style={{height: 400, width: "100%"}}>
      <TileLayer
          attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />

      {flights.map((flight, i) => <div key={i}>
        <Marker icon = {greenIcon} position = {[flight.origin[0],flight.origin[1]]}/>
        <Marker icon = {greenIcon} position = {[flight.destination[0],flight.destination[1]]}/>
          <Marker  icon = {avionIcon} position = {lastItem(flight.code)}   onmouseover={(e) => {e.target.openPopup();}} onmouseout={(e) => {e.target.closePopup();}}>
            <Popup>
              {flight.code}
            </Popup>
          </Marker>
        <Polyline color={'red'} positions={filterItems(flight.code).map(elem => elem.position)} />
        <Polyline dashArray = {5, 30} color={'yellow'} positions={[[flight.origin[0],flight.origin[1]], [flight.destination[0],flight.destination[1]]]} />
      </div>)}

      </Map></div>
    );
  }
  
  export default MapComponent;
